export const fetchComments = () => {
    const data = [
        { author: "Developer", body: "You look nice today." },
        { author: "Aaron Morey", body: "I know, right?!" }
    ];

/*     return function(dispatch){
        dispatch(fetchCommentsSuccess(comments));
    } */

    return {
        type: 'FETCH_COMMENTS',
        payload: data
      };
}

// Actions must serializable
// Action Creators to create Actions
/*

Smart & Dumb Components
Container = Smart
-- Aware of Redux
-- Subscribe to Redux State
-- Dispatch Redux Actions
-- Generated by react-redux

Components = Dumb
-- Unaware of Redux
-- Read data from Props
-- Invoke Callbacks on Props
-- Written by hand

react-redux
-- Connects React and Store
-- Redux is used in other libraries

Action > Store
Store <> Reducers
Store > React

Provider - App Root Wrap & Attaches app to store
Connect - Creates container components

State & Actions to Components
export default connect (
    mapStateToProps,
    mapDispatchToProps
)(Component);

mapStateToProps
- What state should be exposed as props to component
- Subset of State from Store

Reselect Library - Function based Caching based on Params
- memoize for performance
- expensive operations

mapDispacthToProps
- What actions do you want to expose on props

3 ways to handle mapDispatchToProps
-- 1. Ignore it & Use dispatch
-- Child components need to reference redux / action creators
-- Not ideal
this.props.dispatch(function());

-- 2. Expose functions with dispatches in container to Component
function mapDispatchToProps(dispatch) {
    return{
        funcOne: () => {
            dispatch(function());
        },
        funcTwo: () => {
            dispatch(functionTwo());
        }
    }
}

-- In component
this.props.funcOne();

-- 3. bindActionCreators - Redux
-- Same as 2 but passing actions to props

this.props.actions.funcOne();
*/